*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

a {
  display: inline-block;
  text-decoration: none;
}

body {
  font-size: $font-size-base;
  line-height: $line-height-base;
  font-family: $font-family-base;
}
